.header {
  display: flex;
  align-items: center;
  padding: 45px 0 40px 0;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 890px) {
  .header {
    padding: 45px 20px 40px 20px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding: 28px 0 31px 27px;
  }
}
